
body {
    background-image: url("../minihoneycomb.jpeg");
    background-repeat: repeat;
  }

h5 {
    margin-top: 1px;
    margin-bottom: -15px;
}

.centered { 
    margin: auto;
    width: 50%;
    padding: 10px;
}

.upload-file {
    color: white;
    background-color: grey;
}

.input-div {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
    width: 300px;
    margin-left: 30%;
}

.input {
  width: 150px;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.upload-btn {
    width: 250px;
    height: 30px;
    
    background-color: #4CAF50;
    border: none;
    color: white;
    border-radius: 4px;
}

.upload-btn:hover {
    background-color: #45a049;
}