.input {
    width: 250px;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }

.input-name-lbl {

}

.login-container {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
    width: 700px;
}

.centered { 
    margin: auto;
    width: 50%;
    padding: 10px;
}

.login-btn {
    width: 250px;
    height: 30px;
    background-color: #4CAF50;
    border: none;
    color: white;
    border-radius: 4px;
}

.toggle-login-view {
    width: 250px;
    height: 30px;
    background-color: rgb(190, 190, 190);
    border: none;
    color: white;
    border-radius: 4px;
    margin-top: 10px;
}