.App {
  text-align: center;
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background: rgb(59, 59, 160);
  color: white;
}

.nav-link {
  margin: 5px;
  color: white;
  background-color: transparent;
}

.nav-links {
  /* //width: 50%; */
  display: flex;
  align-items: center;
  list-style: none;
}

.nav-link:hover {
  background-color: lightblue;
}

.page-title {
  margin-top: 0px;
  padding-bottom: 8px;
  background-color: rgb(59, 59, 160);
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.side-margin {
  
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* AUDIOTRACK COMPONENT START */

.audio-track-container {
  width: 94%;
  margin-left: 2%;
  justify-content: left;
  margin-top: 5px;
  background-color: rgb(95, 192, 95);
  color: white;
  padding: 5px;
  border: 2px solid black;
}

.track-title {
  margin-right: 8px;
  margin-left: 8vh;
}

.play-track-btn {
  margin-right: 8px;
  background-color: rgb(59, 59, 160);
  color: white;
  width: 100px;
}
.play-track-btn:hover {
  background-color: rgb(106, 106, 184);
}

.track-progress-bar {
  height: 20px;
  color: green;
}

#track-progress-bar>div {
  background-color: orange;
  width: 40%;
  /* Adjust with JavaScript */
  height: 20px;
  border-radius: 10px;
}

/* END */

#loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

